import { useState } from "react";

/**
 * Хук для принудительного обновления компонента
 * @return {Function} Функция, которая вызывает обновление состояния компонента.
 */
export function useForceUpdate() {
  /**
   * Состояние, используемое только для того, чтобы вызвать обновление
   * @type {Object}
   */
  const [state, setState] = useState({});

  /**
   * Функция, которая обновляет состояние компонента и заставляет его выполнить перерисовку
   * @return {void}
   */
  return () => setState({});
}
